import React, { Component } from 'react';
import classes from './Auth.css';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';

import Input from '../../components/UI/Input/Input';
import Button from '../../components/UI/Button/Button';
import Spinner from '../../components/UI/Spinner/Spinner';
import * as actions from '../../store/actions/index';
import { updateObject, checkValidity } from '../../shared/utility';

class Auth extends Component {
    state = {
        controls: {
            email: {
                elementType: 'input',
                elementConfig: {
                    type: 'email',
                    placeholder: 'E-Mail Address'
                },
                value: '',
                validation: {
                    required: true,
                    isEmail: true
                },
                valid: false,
                touched: false,
            },
            password: {
                elementType: 'input',
                elementConfig: {
                    type: 'password',
                    placeholder: 'Your Password'
                },
                value: '',
                validation: {
                    required: true,
                    minLength: 6
                },
                valid: false,
                touched: false,
            }
        },
        isSignup: true,
    }

    componentDidMount() {
        if (!this.props.buildingBurger && this.props.authRedirectPath !== '/react16_demo_project/') {
            this.props.onSetAuthRedirectPath()
        }
    };

    

    inputChangeHandler = (event, controlName) => {
        const updatedControls = updateObject(this.state.controls, {
            [controlName]: updateObject(this.state.controls[controlName], {
                value: event.target.value,
                valid: checkValidity(event.target.value, this.state.controls[controlName].validation),
                touched: true
            })
        });
        this.setState({ controls: updatedControls });
    };

    submitHandler = (event) => {
        event.preventDefault();
        this.props.onAuth(this.state.controls.email.value, this.state.controls.password.value, this.state.isSignup);
    }

    switchAuthModeHandler = () => {
        this.setState(prevState => {
            return { isSignup: !prevState.isSignup };
        });
    };

    render() {
        const formElementsArray = [];
        for (let key in this.state.controls) {
            formElementsArray.push({
                id: key,
                config: this.state.controls[key]
            });
        }

        let form = (
            <form onSubmit={this.submitHandler}>
                {formElementsArray.map(singleElement => (
                    <Input
                        key={singleElement.id}
                        elementType={singleElement.config.elementType}
                        elementConfig={singleElement.config.elementConfig}
                        value={singleElement.config.value}
                        invalid={!singleElement.config.valid}
                        shouldValidate={singleElement.config.validation}
                        touched={singleElement.config.touched}
                        changed={(event) => this.inputChangeHandler(event, singleElement.id)} />
                ))}
                <Button
                    btnType='Success' >SUBMIT</Button>
            </form>)

        if (this.props.loading) {
            form = <Spinner />;
        }

        let errorMessage = null;
        /* message is a Firebase error JavaScript object */
        if (this.props.error) {
            errorMessage = (
                <p className={classes.Error}>Error: {this.props.error.message}</p>
            );
        }

        let authRedirect = null;
        if (this.props.isAuthenticated) {
            authRedirect = <Redirect to={this.props.authRedirectPath} />
        }

        return (
            <div className={classes.Auth}>
                {authRedirect}
                <h2>{this.state.isSignup ? 'REGISTER FORM' : 'LOGIN FORM'}</h2>
                {form}
                {errorMessage}
                <Button
                    btnType='Danger'
                    clicked={this.switchAuthModeHandler}>SWITCH TO {this.state.isSignup ? 'SIGN IN' : 'SIGN UP'}</Button>
            </div>
        );
    }
};

const mapStateToProps = state => {
    /* rootReducer > object of states > state */
    return {
        loading: state.auth.loading,
        error: state.auth.error,
        isAuthenticated: state.auth.token !== null,
        buildingBurger: state.burgerBuilder.buildedBurger,
        authRedirectPath: state.auth.authRedirectPath
    };
};

const mapDispatchToProps = dispatch => {
    return {
        onAuth: (email, password, isSignup) => dispatch(actions.auth(email, password, isSignup)),
        onSetAuthRedirectPath: () => dispatch(actions.setAuthRedirectPath('/react16_demo_project/'))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Auth);


